.App {
  text-align: center;
  background-color: gray;
  min-height: 100vh;
  height: 100%;
}

/* ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* NAVBAR ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
.navbar-container {
  background-color: lightgoldenrodyellow;
  width: 100%;
  height: 3.5rem;
}

.mobile-nav {
  display: none;
}

.navbar-ul {
  margin: 0%;
  display: inline-block;
  margin-left: 60vmin;
}

.navbar-ul li {
  display: inline-block;
  margin-top: 0;
}

.nav-link {
  color: #333;
  text-decoration: none;
  font-family: 'Bad Script', cursive;
  font-size: 2rem;
  padding: 2rem;
}

.nav-link:hover {
  background: grey;
  color: lightgoldenrodyellow;
  padding: 2rem;
}

/* Profile Picture */
.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  width: 60vmin;
  pointer-events: none;
  position: absolute;
  top: 2rem;
  border-bottom: solid 0.3rem lightgoldenrodyellow;
  border-right: solid 0.3rem lightgoldenrodyellow;
  padding-right: .3rem;
  padding-bottom: .3rem;
}

/* ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* HOME PAGE ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/

.App-header {
  display: flex;
  flex-direction: row;
}

.home-container {
  display: inline-block;
  width: 40%;
  margin-left: 45%;

}

.home-heading {
  color: lightgoldenrodyellow;
  font-family: 'Bad Script', cursive;
  font-size: 5rem;
  border: solid .3rem lightgoldenrodyellow;
  padding: 0 1.5rem;
  margin-bottom: 0;
  text-align: left;
}

.home-bio {
  background-color: lightgoldenrodyellow;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-family: 'Avenir', 'Helvetica', 'sans-serif';
  color:#333; 
  text-align: left;
}

/* ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* ABOUT PAGE ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
.about-container {
  display: inline-block;
  width: 40%;
  margin-left: 30%;
}

.about-heading {
  color: lightgoldenrodyellow;
  font-family: 'Bad Script', cursive;
  font-size: 5rem;
  border: solid .3rem lightgoldenrodyellow;
  padding: 0 1.5rem;
  margin-bottom: 0;
  text-align: left;
}

.about-bio {
  background-color: lightgoldenrodyellow;
  margin-top: 0;
  margin-bottom: 0;
  padding-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem;
  font-family: 'Avenir', 'Helvetica', 'sans-serif';
  color:#333; 
  text-align: left;
}

/* ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* LOGOS PAGE ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
.logo-grid {
  top: 20%;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-column-gap: 0.5rem;
  grid-template-rows: repeat(2, 30vh);
  grid-row-gap: 0.5rem;
  width: 80%;
  height: 80vh;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}

.logo-grid-hov:hover {
  background-color: lightgoldenrodyellow;
  opacity: 0.8;
}

.logo-grid-box1 {
  grid-column-start: 1;
  grid-column-end: 2;
  background-color: darkgray;
  background-image: url('./images/caltonLabsLogo.png');
  background-repeat: no-repeat;
  background-size: 65%;
  background-position: center;
}

.logo-grid-box2 {
  grid-column-start: 2;
  grid-column-end: 3;
  background-color: darkgray;
  background-image: url('./images/drawingFowlsLogoMain.png');
  background-repeat: no-repeat;
  background-size: 75%;
  background-position: center;
}

.logo-grid-box3 {
  grid-column-start: 3;
  grid-column-end: 4;
  background-color: darkgray;
  background-image: url('./images/drawingFowlsLogoAlt.png');
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center;
}

.logo-grid-box4 {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 2;
  background-color: darkgray;
  background-image: url('./images/a&mCoLogoMain.png');
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
}

.logo-grid-box5 {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 3;
  background-color: darkgray;
  background-image: url('./images/a&mCoLogoAlt.png');
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center;
}

.logo-grid-box6 {
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column-start: 3;
  grid-column-end: 4;
  background-color: darkgray;
  background-image: url('./images/overLandonLogo.png');
  background-repeat: no-repeat;
  background-size: 90%;
  background-position: center;
}

/* ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* WEBSITES PAGE ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
.web-grid {
  top: 30%;
  display: grid;
  grid-template-columns: repeat(3, 30vw);
  grid-column-gap: 0.5rem;
  /* grid-template-rows: repeat(2, 30vh);
  grid-row-gap: 0.5rem; */
  width: 90vw;
  /* /// Adjust the bottom margin of the .df-video class accordingly when changing the height here /// */
  height: 40vh;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}

.web-grid-hov:hover {
  background-color: lightgoldenrodyellow;
  opacity: 0.8;
}

.web-grid-box1 {
  grid-column-start: 1;
  grid-column-end: 2;
  background-color: darkgray;
  /* background-image: url('./videos/drawingFowlsDemo.mp4'); */
  background-repeat: no-repeat;
  background-size: 65%;
  background-position: center;
}

.df-video {
  display: inline-block;
  max-width: 25vw;
  margin-top: 13%;
  margin-bottom: 15%;
}

.web-grid-box2 {
  grid-column-start: 2;
  grid-column-end: 3;
  background-color: darkgray;
}

.a-speaker {
  background-color: darkgray;
  background-image: url('./images/speakerStrapDemo.png');
  background-repeat: no-repeat;
  background-size: 80%;
  background-position: center;
}

.web-grid-box3 {
  grid-column-start: 3;
  grid-column-end: 4;
  background-color: darkgray;
  background-image: url('./images/drawingFowlsLogoAlt.png');
  background-repeat: no-repeat;
  background-size: 60%;
  background-position: center;
}

.a-overland {
  background-color: darkgray;
  background-image: url('./images/overLandDemo.png');
  background-repeat: no-repeat;
  background-size: 85%;
  background-position: center;
}

.a-hov:hover {
  background-color: lightgoldenrodyellow;
  opacity: 0.8;
}

/* ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* CONTACT ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
.contact-container {
  display: inline-block;
  width: 40%;
  margin-left: 30%;
}

/* ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
/* FOOTER ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////*/
.footer {
  position: absolute;
  z-index: auto;
  display: block;
  bottom: 0;
  height: 3.5rem;
  width: 100%;
  background: lightgoldenrodyellow;
  font-family: 'Avenir','Arial';
  overflow: hidden;
}

.social-container {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, 25%) auto;
  width: 20%;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}

.footer-link1 {
  height: 2%;
  grid-column-start: 1;
  grid-column-end: 2;
}

.footer-link2 {
  height: 2%;
  width: auto;
  grid-column-start: 2;
  grid-column-end: 3;
}

.social-container a:hover {
  opacity: 0.3;
}

.social-icon {
  height: 50%;
  width: auto;
  margin: .8rem;
}

.footer-heading {
  font-size: 1rem;
  color: grey;
  position: absolute;
  width: 100%;
  margin-top: 1rem;
  left: 50%;
  transform: translate(-50%);
  grid-column-start: 3;
  grid-column-end: 4;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Portrait and Landscape for iphone X */
@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3) { 

    .App {
      height: 115vh;
    }
    .navbar-ul li {
      display: none;
    }

    /* mobile navbar to replace standard navbar */
    .mobile-nav {
      display: inline-block;
      text-align: center;
      position: absolute;
      right: 3.5vmin;
      top: 1.75vmin;
      background: gray;
      color: lightgoldenrodyellow;
      padding: 3.5vmin;
      
    }

    /* profile picture */
    .App-logo {
      left: 50%;
      transform: translate(-50%);
    }

    /* bio container on home page */
    .home-container {
      width: 100%;
      margin-top: 65vmin;
      margin-left: 0;
    }

    .footer {
      position: absolute;
      overflow: visible;
      top: 230vmin;
    }

    /* icon container within footer */
    .social-container {
      margin-top: 0vmin;
      width: 60%;
      overflow-x: hidden;
    }

    /* about page bio container */
    .about-container {
      width: 100%;
      margin-top: 65vmin;
      margin-left: 0;
    }

    /* logo grid styling */
    .logo-grid {
      grid-template-columns: repeat(2, auto);
      grid-column-gap: 0.5rem;
      grid-template-rows: repeat(3, 20vh);
      grid-row-gap: 0.5rem;
      width: 80%;
      height: 5vh;
      position: absolute;
      left: 50%;
      transform: translate(-50%);
    }

    .logo-grid-box1 {
      grid-column-start: 1;
      grid-column-end: 2;
      background-color: darkgray;
      background-image: url('./images/caltonLabsLogo.png');
      background-repeat: no-repeat;
      background-size: 65%;
      background-position: center;
    }
    
    .logo-grid-box2 {
      grid-column-start: 2;
      grid-column-end: 3;
      background-color: darkgray;
      background-image: url('./images/drawingFowlsLogoMain.png');
      background-repeat: no-repeat;
      background-size: 75%;
      background-position: center;
    }
    
    .logo-grid-box3 {
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 2;
      background-color: darkgray;
      background-image: url('./images/drawingFowlsLogoAlt.png');
      background-repeat: no-repeat;
      background-size: 60%;
      background-position: center;
    }
    
    .logo-grid-box4 {
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 2;
      grid-column-end: 3;
      background-color: darkgray;
      background-image: url('./images/a&mCoLogoMain.png');
      background-repeat: no-repeat;
      background-size: 80%;
      background-position: center;
    }
    
    .logo-grid-box5 {
      grid-row-start: 3;
      grid-row-end: 4;
      grid-column-start: 1;
      grid-column-end: 2;
      background-color: darkgray;
      background-image: url('./images/a&mCoLogoAlt.png');
      background-repeat: no-repeat;
      background-size: 60%;
      background-position: center;
    }
    
    .logo-grid-box6 {
      grid-row-start: 3;
      grid-row-end: 4;
      grid-column-start: 2;
      grid-column-end: 3;
      background-color: darkgray;
      background-image: url('./images/overLandonLogo.png');
      background-repeat: no-repeat;
      background-size: 90%;
      background-position: center;
    }

    /* website previews grid styling */
    .web-grid {
      top: 15%;
      display: grid;
      grid-template-rows: repeat(3, 50vw);
      grid-template-columns: repeat(1, 80vw);
      grid-row-gap: 0.5rem;
      /* /// Adjust the bottom margin of the .df-video class accordingly when changing the height here /// */
      position: absolute;
      margin-left: 5%;
    }
    
    
    .web-grid-box1 {
      grid-row-start: 1;
      grid-row-end: 2;
      grid-column-start: 1;
      grid-column-end: 2;
      background-color: darkgray;
    }
    
    .df-video {
      display: inline-block;
      max-width: 65vw;
      margin-top: 10%;
      margin-bottom: 6%;
    }
    
    .web-grid-box2 {
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 2;
      background-color: darkgray;
    }
    
    .a-speaker {
      background-color: darkgray;
      background-image: url('./images/speakerStrapDemo.png');
      background-repeat: no-repeat;
      background-size: 80%;
      background-position: center;
    }
    
    .web-grid-box3 {
      grid-row-start: 3;
      grid-row-end: 4;
      grid-column-start: 1;
      grid-column-end: 2;
      background-color: darkgray;
    }
    
    .a-overland {
      background-color: darkgray;
      background-image: url('./images/overLandDemo.png');
      background-repeat: no-repeat;
      background-size: 80%;
      background-position: center;
    }

}
